import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function capexPerShareTtm(tableName: string) {
  const title = "Capex/Share (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["capexPerShareTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Capital expenditures per share.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about capex per share or other relevant financial measures?`,
              value: info.row.original.financialData.companyRatios?.data?.capexPerShareTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyRatios?.data?.capexPerShareTtm}
        />
      )
    },
  })
}

export { capexPerShareTtm }
